<script>
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  getInventoryFinanceCompanyOpen, //财务或库存
  getCustomerList,
  tableResize, //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "reportReceiptdetail",
  data() {
    return {
      listShow: false,
      loadings: false,
      authority: {},
      outerVisible: false,
      TableData: {
        tableData: [],
        allList: {},
        total: 10,
      },
      warehouseList: {
        options: [],
      }, //仓库管理
      logisticsList: {
        options: [],
      }, //物流公司
      businessTypeList: {
        options: [],
      }, //业务类型列表
      params: {},
      searchParams: {},
      goPage: 1, //默认搜索页面
      pageSize: 30,
      loading: true,
      per_page: [30, 50, 100, 500],
      timeValue: [],
      companyList: [],
      multipleSelection: [], //已选项
      is_shop: 0,
      brandsList: [], //品牌管理相关
      list: [], //品牌管理相关
      tableHeight: 600,
    };
  },
  methods: {
    ...mapActions(["salesorderSee", "salesoutfindSell"]),
    getBusinsstype() {
      this.$common.options("businessTypeListOption", this.businessTypeList, {
        is_receipt: 1,
      }); //业务类型列表
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error",
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod.exportReceiptUrl(s).then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getCompany() {
      getInventoryFinanceCompanyOpen(this, {
        type: 2,
      }); //获取组织
    },
    outstock_sn(row) {
      this.$router.push({
        path: "/sales/salesorder",
      });
      this.salesorderSee({
        moduleName: "salesorderSee",
        order_id: row.order_id,
      });
    },
    order_sn(row) {
      this.$router.push({
        path: "/sales/salesoutfind",
      });
      this.salesoutfindSell({
        commont: "LookOver",
        outstock_id: row.outstock_id,
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (
            index == 13 ||
            index === 14 ||
            index === 15 ||
            index === 16 ||
            index === 17
          ) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index == 11) {
            sums[index] = parseFloat(sums[index]);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    logisticsFocus() {
      this.$common.options("getLogisticsListOption", this.logisticsList); //物流
    },
    paymentMethodChange() {
      this.$set(this.params, "settlement_customer_id", null); //回款方式
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    companyChange(val) {
      this.$set(this.params, "business_id", null);
      this.$set(this.params, "company_id", val);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        auditor_status: 0,
      });
      this.timeValue = [];
      this.listShow = false;
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "reportGetReceiptList", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams, 2);
    },
    onSearch(formName) {
      //查询
      var vm = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (!this.params.finance_company_id) {
            this.$message({
              message: "请先选择财务组织",
              type: "error",
            });
          } else {
            this.listShow = true;
            if (this.timeValue) {
              this.params.start_time = this.timeValue[0];
              this.params.end_time = this.timeValue[1];
            } else {
              this.params.start_time = this.params.end_time = "";
            }
            Object.assign(this.searchParams.params, this.params, {
              page: 1,
            });
            getData(this.searchParams, 2, tableResize.bind(this));
          }
        }
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams, 2);
    },
  },
  mounted() {
    this.companyList = [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name,
      },
    ];
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {});
    this.$set(this, "params", {
      finance_company_id: Number(localStorage.company_id),
      company_id: Number(localStorage.company_id),
    });
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "reportGetReceiptList", //接口名
      vm: this, //传递的this
    });
    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  },
};
</script>
